import React, { Fragment, memo } from "react";
import ReactHtmlParser from 'react-html-parser'; 
import './content.scss'

const PostContentHighlight = ({ content, CardStyle }) => {
    const { background, backgroundStatus, color, colorStatus, feedHashtag, allHashtag } = CardStyle?.hashtag || {};
    
    const customHashtagStyle = {
        color: colorStatus ? color : 'inherit',
        fontWeight: "bold",
        backgroundColor: backgroundStatus ? background : 'transparent',
        display: 'inline-block',
        margin: '0 2px'
    };

    if (!content) return null;

    return (feedHashtag || allHashtag) ? (
        content.split(/(\s+)/).map((word, index) => {
                if (word.startsWith('#')) {
                    return (
                        <div key={index} className="tb_highlight" style={customHashtagStyle}>
                            {word}
                        </div>
                    );
                } else if (word.includes('\n')) {
                    return word.split('\n').map((part, i, arr) => (
                        <React.Fragment key={`${index}-${i}`}>
                            {part}
                            {i < arr.length - 1 && <br />}
                        </React.Fragment>
                    ));
                } else {
                    return <React.Fragment key={index}>{word}</React.Fragment>;
                }
            })
    ) : (
        ReactHtmlParser(content)
    );
};

export default memo(PostContentHighlight);